import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthServiceService } from 'src/app/services/auth.service';



declare interface RouteInfo {
  path: string;
  title: string;
  icon: string;
  class: string;
}
export const ROUTES: RouteInfo[] = [
 // { path: '/user', title: 'user',  icon: 'ni-tv-2 text-primary', class: '' },
  { path: '/dashboarduser', title: 'Dashboard',  icon: 'ni-tv-2 text-primary', class: '' },
  { path: '/colisuser', title: 'colis',  icon:'fas fa-box-open text-yellow', class: '' },
  { path: '/suivreuser', title: 'suivrecolis',  icon:'fas fa-box text-pink', class: '' },


 /* { path: '/reclamation', title: 'reclamation',  icon:'ni-circle-08 text-pink', class: '' },*/
  { path: '/profil', title: 'profil',  icon:'ni-circle-08 text-danger', class: '' },
  { path: '/pickup', title: 'pick up',  icon:'fas fa-print text-danger', class: '' },
/*
  { path: '/login', title: 'Login',  icon:'ni-key-25 text-info', class: '' },
  { path: '/register', title: 'Register',  icon:'ni-circle-08 text-pink', class: '' },
 */

 

  
];

@Component({
  selector: 'app-sidebaruser',
  templateUrl: './sidebaruser.component.html',
  styleUrls: ['./sidebaruser.component.css']
})
export class SidebaruserComponent implements OnInit {

  username  : string;
  public menuItems: any[];
  public isCollapsed = true;
private isLoggedin :boolean;

  constructor(private router:Router,private authservice:AuthServiceService) { }

  ngOnInit(): void {
    this.isLoggedin =this.authservice.isLoggedIn();
    this.authservice.loginSuject.asObservable().subscribe(()=>{
      this.isLoggedin = this.authservice.isLoggedIn();
      this.username = this.authservice.getUserName();
    })
     this.menuItems = ROUTES.filter(menuItem => menuItem);
     this.router.events.subscribe((event) => {
       this.isCollapsed = true;
    });
   }
  }

