import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Reclamation } from 'src/app/models/reclamation';
import { ServicereclamationService } from 'src/app/services/servicereclamation.service';

@Component({
  selector: 'app-reclamationuser',
  templateUrl: './reclamationuser.component.html',
  styleUrls: ['./reclamationuser.component.css']
})
export class ReclamationuserComponent implements OnInit {
rec:Reclamation;
messsage:string;
  constructor(private toastr: ToastrService,private reclamationservice:ServicereclamationService,private router: Router) { 
    this.rec=new Reclamation;
    
    this.router.routeReuseStrategy.shouldReuseRoute =() =>{return false;};
  }

  ngOnInit(): void {
  }
  
  public opened = true;
  public dataSaved = false;

  public close() {
    this.opened = false;
  }

  public open() {
    this.opened = true;
  }

  public submit() {
      this.dataSaved = true;
      this.close();
  }

  //console.log(this.rec);
  onSubmit(){this.reclamationservice.Save(this.rec).subscribe(result =>{
    this.messsage="vous avez envoyer la reclamation";this.toastr.success("vous avez envoyer une reclamation");
    this.router.navigate(['profil']);});

};

}
