import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthServiceService } from 'src/app/services/auth.service';
import { Codecoli } from 'src/app/models/codecoli';
import { Etatcolis } from 'src/app/models/etatcolis';
import { ColisService } from 'src/app/services/colis.service';
import { Colis } from 'src/app/models/colis';

@Component({
  selector: 'app-suivrecolisuser',
  templateUrl: './suivrecolisuser.component.html',
  styleUrls: ['./suivrecolisuser.component.css']
})
export class SuivrecolisuserComponent implements OnInit {
  code:Codecoli;
  tablec:Etatcolis[];
  test:boolean;
  colies:Colis;
  items = [

  
    "Audepot",
    "Encours",
    "Rtndepot",
    
    "Retourrecu",
    "Retourdefinitif",
    "Retourexpediteur",
    "livr paye"
    
    ];
    constructor(private colisservice:ColisService) {
      this.code=new Codecoli;
      this.test=false;
      this.colies=new Colis();
     }

    ngOnInit(): void {
    }
    onChercher(f:any){
      //console.log(f.keyword);
      this.test=true;
      this.colisservice.Suivrecolis(f.keyword).subscribe(data =>{this.tablec=data;} );
    }
    onCherche(f:any){
      // console.log(f.keyword);
       this.test=true;
       this.colisservice.cherchepariduser(f.keyword).subscribe(data=>{this.colies=data;
      this.tablec=data.etatcolis;} );
      //console.log(this.colies);
     }
  }
