import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Client } from 'src/app/client';
import { Colis } from 'src/app/models/colis';
import { ColisService } from 'src/app/services/colis.service';

@Component({
  selector: 'app-printcolis',
  templateUrl: './printcolis.component.html',
  styleUrls: ['./printcolis.component.css']
})
export class PrintcolisComponent implements OnInit {

  id:number;
myDate =Date.now();
colies = new Colis();
client=new Client();
  constructor(private colisservice:ColisService,private route:ActivatedRoute,private router:Router) { 
    this.colies = new Colis();
    this.client = new Client();
  }

  ngOnInit(): void {
    this.id=this.route.snapshot.params.id;
    console.log(this.id);
    this.colisservice.cherchepariduser(this.id).subscribe(data=>{this.colies=data;});
    this.exp();

  }

exp(){
  this.colisservice.chercheuserprint(this.id).subscribe(data =>{this.client=data;console.log(data);})
}


}
