import { Component, OnInit } from '@angular/core';
import { ChartDataSets, ChartOptions, Chart } from 'chart.js';
import { Color, Label } from 'ng2-charts';
import { HttpClient } from "@angular/common/http";
import { Colis } from 'src/app/models/colis';
import { ColisService } from 'src/app/services/colis.service';

 interface Datafr {
   month : string  ; 
   value :number; 
 }

@Component({
  selector: 'app-chartuser',
  templateUrl: './chartuser.component.html',
  styleUrls: ['./chartuser.component.css']
})
export class ChartuserComponent implements OnInit {
  datafr :Colis[];

  dlike : number[]=[];
  fg : string[] =[]; 
  lineChartData: ChartDataSets[] = [ {data: [],label:''}];

  lineChartLabels: Label[]=[];
    
    
    lineChartOptions = {
      responsive: true,
  
    };
  
    lineChartColors: Color[] = [
      {
        borderColor: 'white',
       
      },
    ];
  
    lineChartLegend = true;
    lineChartPlugins = [];
    lineChartType = 'line';
  constructor(private colisService:ColisService,private http: HttpClient) { 
    this.dlike  =[];
  this.datafr=[]
   this.fg = [];
  }

  
  

   getDatafromBack(){


     return this.colisService.colislivrepaye().subscribe(data=>{
    
       this.datafr = data ;
         //console.log(data);
       this.datafr.forEach(res=>{
         this.dlike.push(res.montantuser); 
         
          
         this.fg.push( new Date( res.date ).toLocaleString());
         //console.log(res.date);
       });

        
 

       this.lineChartData = [
        { data: this.dlike, label: 'montantuser' }, 
      ];
      
      this.lineChartLabels  = this.fg;   

     });
   }
  ngOnInit(): void {
    this.getDatafromBack();

    
    //['January', 'February', 'March', 'April', 'May', 'June'];
  
   
  }
}
